@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
.login_wrapper_container{width:100vw;height:100vh;padding:0px;margin:0px;background:#00A1E4;background-size:100vw 100vh;background-position:fixed;background-repeat:no-repeat;min-height:90vh;min-width:90vw}.login_wrapper{padding:0px;margin:0px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);max-width:700px;max-height:750px;min-width:250px;min-height:100px;width:95vw;height:70vh;border:2px solid black;background:white;box-shadow:0px 0px 20px 5px black;border-radius:20px;overflow:hidden}.login_wrapper form{width:50%;position:absolute;left:50%;top:50%;-webkit-transform:translate(0%, -50%);transform:translate(0%, -50%);padding:5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding-top:15px}.login_wrapper .MuiTextField-root{margin:20px 0px !important}@media (max-width: 600px){.login_wrapper{height:45vh}.login_wrapper form{width:100%;left:0%}}

.form_sidebar{width:50%;height:100%;padding:0px;margin:0px;position:absolute;background:url(/student_diary/static/media/diary.4ecea9b9.jpg);background-size:cover;top:0%}.form_sidebar h2{position:absolute;bottom:5px;right:5px;text-transform:capitalize;font-family:'Playfair Display', serif;text-align:right;color:#DC0073}@media (max-width: 600px){.form_sidebar{display:none}}

body{background-color:white !important}.progress_wrapper{position:absolute;top:50%;left:50%;width:100%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}

.note{padding:10px;min-height:20px;border-radius:5px;box-shadow:0px 0px 5px 2px #424242;margin:10px;color:white;font-size:20px;font-family:'Playfair Display', serif}.note:hover{font-size:25px;cursor:pointer;box-shadow:0px 0px 5px 2px black}.note .title{color:white}

h1.title{font-family:'Playfair Display', serif;text-align:center;margin-top:5px;margin-bottom:5px;text-decoration:underline;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.change{width:100%}.preview{color:white;background-color:black;padding:5px;box-shadow:0px 0px 5px 1px black}

.create_note{width:50vw;max-width:500px;min-width:250px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:#f7f4f0;box-shadow:0px 0px 5px 1px black;padding:10px}.create_note .btngroup{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;margin:10px 0px}

.profile_form{padding-top:20px}.profile_form .MuiInputBase-root{margin:20px 5px}

body{padding:0px;margin:0px;background:#00A1E4;background-size:100vw 100vh;background-position:fixed;background-repeat:no-repeat;min-height:90vh;min-width:90vw}.signup_wrapper{padding:0px;margin:0px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);max-width:700px;max-height:750px;min-width:250px;min-height:100px;width:95vw;height:70vh;border:2px solid black;background:white;box-shadow:0px 0px 20px 5px black;border-radius:20px;overflow:hidden}.signup_wrapper form{width:50%;position:absolute;left:50%;top:50%;-webkit-transform:translate(0%, -50%);transform:translate(0%, -50%);padding:5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding-top:15px}.signup_wrapper .MuiTextField-root{margin:20px 0px !important}@media (max-width: 600px){.signup_wrapper{height:45vh}.signup_wrapper form{width:100%;left:0%}}

